<template>
  <div>
    <div id="map"></div>
    <div id="picture"></div>
  </div>
</template>

<script>
  import AMapLoader from '@amap/amap-jsapi-loader'
  import site from '@/assets/site.png'
  import {getSite} from '@/api/search'
  import Vue from 'vue'
  import picture from '@/components/base/site-picture'

  const contentDOM = Vue.extend(picture)

  export default {
    name: 'v-map',
    data() {
      return {
        map: null,
        AMap: null,
        marker: []
      }
    },
    mounted() {
      this.init()
    },
    activated() {
      if (this.map && this.AMap) {
        this.restart()
      }
    },
    deactivated() {
      this.map.clearInfoWindow()
    },
    destroyed() {
      document.removeEventListener('touchstart', this.closeInfoWindow)
    },
    methods: {
      closeInfoWindow(e) {
        if (e.target.className === 'amap-layer') {
          this.map.clearInfoWindow()
        }
      },
      init() {
        AMapLoader.load({
          'key': 'c39d5203480dffee4736b96f336a656f',
          'version': '2.0',
          'plugins': []
        }).then((AMap) => {
          this.AMap = AMap
          this.map = new this.AMap.Map('map', {
            zoom: 14
          })
          this.restart()
          /*this.map.plugin('AMap.Geolocation', () => {
            const geolocation = new AMap.Geolocation({
              enableHighAccuracy: true,
              timeout: 10000
            })
            this.map.addControl(geolocation)
            geolocation.getCurrentPosition()
          })*/
        }).catch(e => {
          console.log(e)
        })
      },
      restart() {
        const {AMap, map, marker} = this

        if (marker.length > 0) {
          console.log('clear marker')
          map.remove(marker)
        }

        getSite()
          .then(v => {
            const icon = new AMap.Icon({
              size: new AMap.Size(60, 60),
              image: site,
              imageSize: new AMap.Size(30, 30)
            })
            this.marker = []
            v.forEach(item => {
              const markers = new AMap.Marker({
                position: new AMap.LngLat(...item.lngLat),
                icon: icon,
                title: item.key,
                value: item.value,
                zoom: 13
              })
              const text = new AMap.Text({
                text: item.key,
                cursor: 'pointer',
                style: {
                  fontSize: '12px',
                  'background-color': 'transparent',
                  'border-width': 0,
                },
                offset: new AMap.Pixel(30, 5),
                position: item.lngLat
              })
              text.setMap(this.map)

              // tip
              const content = new contentDOM({
                el: document.createElement('div'),
                data: {
                  src: '',
                  content: item.remarks.join('')
                }
              })
              const infoWindow = new AMap.InfoWindow({
                isCustom: true,
                content: content.$el,
                offset: new AMap.Pixel(0, -30)
              })
              const handlerTouchstart = e => {
                infoWindow.open(map, e.lnglat)
              }
              markers.on('touchstart', handlerTouchstart)

              this.marker.push(markers)
            })
            document.addEventListener('touchstart', this.closeInfoWindow)
            this.map.add(this.marker)
            console.log('add marker')
            this.map.setFitView()
          })
      }
    }
  }
</script>

<style scoped lang="less">

  #map {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #303133;
  }
</style>
