<template>
  <div class="line-map">
    <van-button class="back-btn" icon="arrow-left" color="#00000078" @touchstart="handleBack" />
    <v-map />
    <div class="line-map-site">
      <div class="line-map-site-box">
        <site-list v-if="item.start" :item="item" :jump="false" />
      </div>
    </div>
  </div>
</template>

<script>
  import { mapMutations, mapState } from 'vuex'
  import { SET_ROUTER_MODE } from '@/store/Types/index'
  import vMap from '@/components/map'
  import siteList from '@/components/base/site-list'

  export default {
    name: 'line-map',
    components: {
      vMap,
      siteList
    },
    computed: {
      ...mapState(['currentSite']),
      item() {
        return this.currentSite
      }
    },
    methods: {
      ...mapMutations([SET_ROUTER_MODE]),
      handleBack() {
        const { SET_ROUTER_MODE } = this
        SET_ROUTER_MODE('page-back')
        this.$router.back()
      }
    },

  }
</script>

<style scoped lang="less">
  @import "~@/styles/src/colors";

  .line-map {
    position: relative;
    width: 100%;
    height: 100%;

    &-site {
      position: fixed;
      left: 0;
      bottom: 0;
      width: 100%;
      padding: 10px;
      z-index: 2;

      &-box {
        background-color: white;
        border-radius: 4px;
      }
    }

    .back-btn {
      position: fixed;
      left: 10px;
      top: 10px;
      width: 30px;
      height: 30px;
      padding: 0;
      border: none;
      border-radius: 50%;
      z-index: 2;
    }
  }
</style>
